import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../../state/actions/index';


class InformationTab extends Component {

    componentDidMount = () => {
        const caseId = this.props.caseId;
        const accessToken = this.props.accessToken;


        if (caseId != null || accessToken != null) {
            this.props.getCaseDetails(accessToken, caseId);
            this.props.getCaseFile(accessToken, caseId);
        }
    }

    render() {

        var caseFileResult = this.props.caseFileResult;
        var caseInfo = this.props.caseInfo;
        var displayStatus = null;
        var displayTrans = null;

        if (caseFileResult != null) {
            var tmp = [];
            displayTrans = caseFileResult.map((item) => {
                tmp.push(
                    <tr key={item.recentActivity + "tr"}>
                        <td><b>{item.recentActivity} </b></td>
                        <td>{item.where}</td>
                        <td>{item.amount} </td>
                    </tr>
                );
            });
            displayTrans = tmp;
        } 
        if (caseInfo.status == 1) {
            displayStatus = <p style={{ fontSize: '22px', fontFamily: 'Times New Roman', color: '#0aa826' }} >In Progress</p>;
        } else if (caseInfo.status == 2) {
            displayStatus = <p style={{ fontSize: '22px', fontFamily: 'Times New Roman', color: '#0aa826' }} >Completed</p>;
        } else if (caseInfo.status == 3) {
            displayStatus = <p style={{ fontSize: '22px', fontFamily: 'Times New Roman', color: 'red' }} >Cancelled</p>;
        }
        return (
            <div className="ibox float-e-margins ">
                <div className="ibox-title bluetop">
                    <h2>Case ID: {this.props.caseId}</h2>
                </div>
                <div className="ibox-content">
                    <div className="row">
                        <div className="col-md-4" style={{ borderRight: 'solid 2px #DADADA', borderRightStyle: 'dashed', minHeight: '190px' }}>
                            <h3><b>Case Information</b></h3>
                            <p style={{ fontSize: "12px" }}><b>Date Opened: </b></p>
                            <p style={{ fontSize: "12px" }}>{this.props.caseInfo.started} </p>
                            <p style={{ fontSize: "12px" }}><b>Resolution Expected By: </b></p>
                            <p style={{ fontSize: "12px" }}>{this.props.caseInfo.expected} </p>
                        </div>
                        <div className="col-md-8" style={{ minHeight: '190px' }}>
                            <div className="row">
                                <div className="col-md-3" >
                                    <h3><b>Total Value</b></h3>
                                    <p style={{ fontSize: "12px" }} ><b>Transactions: </b>
                                        {
                                            this.props.caseFileResult != null ? this.props.caseFileResult.length : '-'
                                        }
                                    </p>
                                </div>
                                <div className="col-md-5">
                                    <div>
                                        <div className="grey-box text-center" style={{ backgroundColor: '#dee0e2', width: '60%', height: '100%', paddingTop: '10px', paddingLeft: '15px', paddingRight: '10px' }}>
                                            <p style={{ fontSize: '22px', fontFamily: 'Times New Roman' }} >${this.props.totalAmount}</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div>
                                        <div className="grey-box text-left" style={{ width: '100%', height: '100%', paddingTop: '10px', paddingLeft: '15px', paddingRight: '10px' }}>
                                            {displayStatus}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <br /><br />
                            <div style={{ overflowY: 'scroll', height: '90px' }}>
                                <table style={{ width: "100%" }}>
                                    <tbody>
                                        {displayTrans}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
const mapStateToProps = state => {
    return {
        userProfile: state.transactionDisputeReducer.userProfile,
        caseInfo: state.transactionDisputeReducer.caseInfo,
        caseFileResult: state.transactionDisputeReducer.caseFileResult,
        totalAmount: state.transactionDisputeReducer.totalAmount,
        caseId: state.transactionDisputeReducer.caseId,
        accessToken: state.transactionDisputeReducer.accessToken,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        remoteGetUserProfile: (accessToken) => dispatch(actions.remoteGetUserProfile(accessToken)),
        remoteGetNotificationList: (accessToken, currentCaseId) => dispatch(actions.remoteGetNotificationList(accessToken, currentCaseId)),
        getCaseDetails: (accessToken, currentCaseId) => dispatch(actions.getCaseDetails(accessToken, currentCaseId)),
        getCaseFile: (accessToken, currentCaseId) => dispatch(actions.getCaseFile(accessToken, currentCaseId)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(InformationTab);



