import request from "superagent/superagent";
import * as actionTypes from './actionTypes';
import * as constant from './../../shared/constant';
import * as utility from './../../shared/utility';
import * as action from './index';



export const getCaseDetails = (accessToken, currentCaseId) => {
    return dispatch => {
        dispatch(action.restCallStart());
        request
            .get(constant.GET_CASE_INFO_URL + currentCaseId)
            .set('Content-Type', 'application/json')
            .set('Accept', 'application/json')
            .set('Authorization', 'Bearer ' + accessToken)
            .then((res) => {
                dispatch(action.restCallSuccess());

                //   console.log("This is info: " + JSON.stringify(res));
                var tmp = {};
                tmp.id = res.body["case-id"];
                tmp.description = res.body["case-description"];
                tmp.owner = res.body["case-owner"];
                tmp.status = res.body["case-status"];
                tmp.caseDefinitionId = res.body["case-definition-id"];
                tmp.containerId = res.body["container-id"];
                tmp.started = (new Date(res.body["case-started-at"])).toLocaleString();
                // build expected date
                var myExpected = new Date(res.body["case-started-at"]);
                myExpected.setDate(myExpected.getDate() + 7);
                tmp.expected = myExpected.toLocaleString();

                dispatch(action.setCaseInfo(tmp));
                // console.log("This is info: " + JSON.stringify(tmp));
            })
            .catch((err) => {
                var tmp = {};

                tmp.errorMessage = err.message;
                tmp.errorResponse = err.response;
                console.log(err);
                dispatch(action.restCallFail(tmp.errorResponse));
            });
    }
}



export const getCaseFile = (accessToken, currentCaseId) => {
    return dispatch => {
        dispatch(action.restCallStart());
        var resultArray = [];
        var totalAmount = 0;
        request
            .get(constant.GET_CASE_INFO_URL + currentCaseId + "/file")
            .set('Content-Type', 'application/json')
            .set('Accept', 'application/json')
            .set('Authorization', 'Bearer ' + accessToken)
            .then((res) => {
                dispatch(action.restCallSuccess());
                //console.log("mock is: " + JSON.stringify(constant.MOCK_CASE_FILE));
                // console.log("response: " + JSON.stringify(res.body));
                var responseBody = constant.MOCK_CASE_FILE.tranxtn;
                //console.log(responseBody);
                for (var i = 0; i < responseBody.length; i++) {
                    var tmp = {};
                    tmp.id = responseBody[i].id;
                    tmp.amount = responseBody[i].Amount;
                    tmp.descriptions = responseBody[i].Description;
                    tmp.where = responseBody[i].extraDescriptions[0].where_you_Paid;
                    tmp.recentActivity = responseBody[i].recentActivity;
                    //console.log(JSON.stringify(tmp));
                    resultArray.push(tmp);
                    totalAmount += responseBody[i].Amount;
                    //                    console.log(resultArray[0]);

                    if (i == responseBody.length - 1) {
                        // console.log(totalAmount);
                        dispatch(action.setCaseFileResult(resultArray));
                        dispatch(action.setTotalAmount(totalAmount));
                        //                        this.setState({ caseFileResult: resultArray, totalAmount: totalAmount });
                    }
                }
                //console.log(JSON.stringify(resultArray));

                //   console.log("This is info: " + JSON.stringify(this.state.caseFileResult));
            })
            .catch((err) => {
                var tmp = {};
                console.log("Failed: " + err.message);
                tmp.errorMessage = err.message;
                tmp.errorResponse = err.response;
                dispatch(action.restCallFail(tmp.errorResponse));
            });
    }
}


export const remoteGetComments = (accessToken, currentCaseId) => {
    return dispatch => {
        dispatch(action.restCallStart());
        //    console.log("This is endpoint: " + constant.GET_CASE_INFO_URL + currentCaseId + "/comments");
        request
            .get(constant.GET_CASE_INFO_URL + currentCaseId + "/comments")
            .set('Content-Type', 'application/json')
            .set('Accept', 'application/json')
            .set('Authorization', 'Bearer ' + accessToken)
            //        .auth('pamAdmin', 'Password@100')
            .then((res) => {
                dispatch(action.restCallSuccess());

                var result = [];

                for (var i = 0; i < (res.body.comments).length; i++) {

                    var comment = res.body.comments[i].text;
                    var date = (new Date(res.body.comments[i]["added-at"]["java.util.Date"])).toLocaleString();
                    console.log(comment);
                    try {
                        comment = JSON.parse(comment);
                        result.push(date + "  " + comment.author + " said: " + comment.text);
                    } catch (e) {
                        //  console.log("Not a valid input.");
                        result.push(date + "  " + res.body.comments[i].author + " said: " + res.body.comments[i].text);
                    }
                    // result.push(date + "  " + comment.author + " said: " + comment.text);
                }
                //                dispatch(action.getComments(result));
                dispatch({ type: actionTypes.GET_COMMENTS, payload: result });

                //console.log("This is successful, result is :  " + result);
            })
            .catch((err) => {
                var tmp = {};
                console.log(err.message);
                tmp.errorMessage = err.message;
                tmp.errorResponse = err.response;
                dispatch(action.restCallFail(tmp.errorResponse));
            });
    }
}



export const remotePostComments = (accessToken, currentCaseId, input) => {
    return dispatch => {
        dispatch(action.restCallStart());
        //    console.log("This is endpoint: " + constant.GET_CASE_INFO_URL + currentCaseId + "/comments");


        //console.log("Input is: " + JSON.stringify(JSON.stringify(input)));

        var userinput = JSON.stringify(input);
        var userinput = userinput.slice(1, -1);
        //console.log(userinput);
        request
            .post(constant.GET_CASE_INFO_URL + currentCaseId + "/comments")
            .set('Content-Type', 'application/json')
            .set('Authorization', 'Bearer ' + accessToken)
            //            .auth('pamAdmin', 'Password@100')
            .send(userinput)
            //    .send("{\\\"Hello\\\":\\\"world\\\"}")
            .then((res) => {
                dispatch(action.restCallSuccess());
                //    console.log("This is endpoint: " + constant.GET_CASE_INFO_URL + currentCaseId + "/comments");
                // console.log('Success, response is: ' + res.body);
                request
                    .get(constant.GET_CASE_INFO_URL + currentCaseId + "/comments")
                    .set('Content-Type', 'application/json')
                    .set('Accept', 'application/json')
                    .set('Authorization', 'Bearer ' + accessToken)
                    //        .auth('pamAdmin', 'Password@100')
                    .then((res) => {
                        dispatch(action.restCallSuccess());

                        var result = [];

                        for (var i = 0; i < (res.body.comments).length; i++) {

                            var comment = res.body.comments[i].text;
                            var date = (new Date(res.body.comments[i]["added-at"]["java.util.Date"])).toLocaleString();
                            console.log(comment);
                            try {
                                comment = JSON.parse(comment);
                                result.push(date + "  " + comment.author + " said: " + comment.text);
                            } catch (e) {
                                //  console.log("Not a valid input.");
                                result.push(date + "  " + res.body.comments[i].author + " said: " + res.body.comments[i].text);
                            }
                            // result.push(date + "  " + comment.author + " said: " + comment.text);
                        }
                        //                dispatch(action.getComments(result));
                        dispatch({ type: actionTypes.GET_COMMENTS, payload: result });

                        //console.log("This is successful, result is :  " + result);
                    })
                    .catch((err) => {
                        var tmp = {};
                        console.log(err.message);
                        tmp.errorMessage = err.message;
                        tmp.errorResponse = err.response;
                        dispatch(action.restCallFail(tmp.errorResponse));
                    });
            })
            .catch((err) => {
                //console.log("failed, ::" + err.message);
                dispatch(action.restCallFail(err.message));
            });
    }

}

export const remoteGetUserProfile = (accessToken) => {
    return dispatch => {
        dispatch(action.restCallStart());

        // console.log("Action " + constant.GET_USER_PROFILE_URL);
        request
            .get(constant.GET_USER_PROFILE_URL)
            .set('Content-Type', 'application/json')
            .set('Accept', 'application/json')
            .set('Authorization', 'Bearer ' + accessToken)
            .then((res) => {
                dispatch(action.restCallSuccess());

                const response = JSON.parse(res.body);
                // console.log("Server Response " + JSON.stringify(response));

                return dispatch(action.userProfile(response));
            })
            .catch((err) => {
                var tmp = {};
                tmp.errorMessage = err.message;
                tmp.errorResponse = err.response;
                console.log(tmp);
                console.log(err);
                dispatch(action.restCallFail(tmp.errorResponse));
            });
    }
}
export const remoteGetNotificationList = (accessToken, status) => {
    return dispatch => {
        dispatch(action.restCallStart());

        // console.log("Action " + constant.GET_NOTIFICATION_HISTORY);
        request
            .get(constant.GET_NOTIFICATION_LIST_ENDPOINT)
            .set('Content-Type', 'application/json')
            .set('Accept', 'application/json')
            .query({ status: status })
            .set('Authorization', 'Bearer ' + accessToken)
            .then((res) => {
                dispatch(action.restCallSuccess());

                const response = res.body;
                // console.log("Server Response " + JSON.stringify(response));

                const output = response;
                // console.log("outputs " + JSON.stringify(output));

                return dispatch(action.getNotificationList(output));
            })
            .catch((err) => {
                var tmp = {};
                tmp.errorMessage = err.message;
                tmp.errorResponse = err.response;
                console.log(tmp);
                console.log(err);
                dispatch(action.restCallFail(tmp.errorResponse));
            });
    }
}


export const remoteCancelDispute = (accessToken, caseId) => {
    return dispatch => {
        dispatch(action.restCallStart());

        // console.log("Action " + constant.GET_USER_PROFILE_URL);
        request
            .post(constant.POST_CANCEL_DISPUTE_ENDPOINT + caseId)
            .set('Content-Type', 'application/json')
            .set('Accept', 'application/json')
            .set('Authorization', 'Bearer ' + accessToken)
            .send(constant.CANCEL_DISPUTE_PAYLOAD)
            .query({ page: 0, pageSize: 1000 })
            .then((res) => {
                dispatch(action.restCallSuccess());
            })
            .catch((err) => {
                var tmp = {};
                tmp.errorMessage = err.message;
                tmp.errorResponse = err.response;
                console.log(tmp);
                console.log(err);
                dispatch(action.restCallFail(tmp.errorResponse));
            });
    }
}


